@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Playfair+Display:wght@400;500;600;700&family=Merriweather:wght@300;400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #0a57a3 0%, #084a8a 50%, #1e293b 100%);
  min-height: 100vh;
  color: #f8fafc;
  line-height: 1.6;
}

.luxury-text {
  font-family: 'Playfair Display', 'Times New Roman', serif;
  font-weight: 600;
  letter-spacing: -0.02em;
}

.modern-text {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  letter-spacing: 0.01em;
}

/* Typography improvements */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: -0.02em;
}

p {
  line-height: 1.7;
  color: #cbd5e1;
}

.text-disney-blue {
  color: #0a57a3;
}

.text-disney-gold {
  color: #FFD700;
}

.text-slate-light {
  color: #94a3b8;
}

.text-neutral-white {
  color: #f8fafc;
}

.glass-effect {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(16px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

/* Button styles */
.btn-primary {
  background: linear-gradient(135deg, #0a57a3 0%, #1e6bb8 100%);
  color: white;
  font-weight: 600;
  padding: 12px 24px;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px rgba(10, 87, 163, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(10, 87, 163, 0.4);
  background: linear-gradient(135deg, #1e6bb8 0%, #0a57a3 100%);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-weight: 500;
  padding: 12px 24px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 215, 0, 0.5);
  transform: translateY(-1px);
}

/* Card styles */
.card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(16px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.15);
  border-color: rgba(255, 215, 0, 0.3);
}

.card-elevated {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 20px;
  padding: 32px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
}

.magic-border {
  position: relative;
  background: linear-gradient(45deg, #D4AF37, #FFD700, #D4AF37);
  background-size: 200% 200%;
  animation: gradient-shift 3s ease infinite;
}

@keyframes gradient-shift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.sparkle::before {
  content: '✨';
  position: absolute;
  top: -10px;
  right: -10px;
  animation: sparkle 2s ease-in-out infinite;
}

.floating {
  animation: float 3s ease-in-out infinite;
}

.glow-effect {
  box-shadow: 0 0 20px rgba(212, 175, 55, 0.5);
  animation: glow 2s ease-in-out infinite alternate;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(45deg, #D4AF37, #FFD700);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(45deg, #FFD700, #D4AF37);
}

/* Loading animation */
.loading-dots {
  display: inline-block;
}

.loading-dots::after {
  content: '';
  animation: loading-dots 1.5s infinite;
}

@keyframes loading-dots {
  0%, 20% { content: ''; }
  40% { content: '.'; }
  60% { content: '..'; }
  80%, 100% { content: '...'; }
}
